import React from "react";
import ReactDOM from "react-dom";
import { Widget } from "@lumino/widgets";
import { Deployment } from "../components/DeploymentDashboard/Deployment";
export class DeploymentWidget extends Widget {
    _app;
    constructor(app) {
        super();
        this._app = app;
        this.addClass("jp-CustomWidget");
        this.addClass("jp-Deployment");
    }
    onAfterAttach() {
        ReactDOM.render(React.createElement(Deployment, { url: this._app.domainUrl }), this.node);
    }
    onBeforeDetach() {
        ReactDOM.unmountComponentAtNode(this.node);
    }
}
