import Cookies from "js-cookie";
function parseJson(data) {
    try {
        return JSON.parse(data);
    }
    catch (error) {
        console.error(error);
        return null;
    }
}
let cachedParams = {};
export function parseParams(cookieName) {
    if (cachedParams && cachedParams[cookieName] != null) {
        return cachedParams[cookieName];
    }
    const paramsB64 = Cookies.get(cookieName);
    // remove the cookie so the user is not prompted again if the page is refreshed
    // Remove the cookie in a timeout, due to the migration to the monorepo. This is a hack but a short-lived one
    setTimeout(() => {
        Cookies.remove(cookieName, {
            path: "/",
            domain: `.${window.location.hostname}`,
            sameSite: "Strict",
        });
    }, 5000);
    if (!paramsB64) {
        return null;
    }
    const data = parseJson(atob(paramsB64.replaceAll(".", "=")));
    if (data == null) {
        return null;
    }
    return (cachedParams[cookieName] = cachedParams[cookieName]
        ? { ...cachedParams[cookieName], ...data }
        : data);
}
export function clearParsedCookieCache() {
    cachedParams = {};
}
