import React from "react";
import { ResourcesList } from "../components/List/ResourcesList";
import { useLMSContentFetch } from "@anaconda/lms_notebooks_lib";
import { FeatureFlag } from "../types";
import { useFeatureFlags } from "../state/apps-zustand/feature-flags";
export const ResourcesView = () => {
    const { categories } = useLMSContentFetch();
    const store = useFeatureFlags.getState();
    const isResourcesFFEnabled = store.evaluateFeatureFlag(FeatureFlag.RESOURCES);
    const courses = categories.flatMap((category) => category.courses);
    return (isResourcesFFEnabled &&
        categories.length > 0 && (React.createElement(ResourcesList, { courses: courses, categories: categories })));
};
