import React, { useMemo } from "react";
import { CategoryTile } from "../Tile/CategoryTile";
import { useHeapAnalytics } from "../../store/heap";
export const CategoryList = ({ categories, onCategoryClick, }) => {
    const heapAnalytics = useHeapAnalytics((state) => ({
        registerEvent: state.registerEvent,
    }));
    const handleClick = useMemo(() => {
        return (category) => {
            heapAnalytics.registerEvent("LMS Notebooks - Category Click", {
                category: category.title,
            });
            onCategoryClick(category.courses, category);
        };
    }, [onCategoryClick]);
    return (React.createElement("div", { className: "anaconda-lms-category-list", "data-testid": "category-list" }, categories.map((category, index) => (React.createElement("div", { "data-testid": `category-tile-${category.title}`, key: category.title, onClick: () => handleClick(category) },
        React.createElement(CategoryTile, { categoryTitle: category.title, coursesCount: category.courses.length }))))));
};
