import React, { useEffect, useState } from "react";
import { Terminal } from "xterm";
import { FitAddon } from "xterm-addon-fit";
import { fetchLogs } from "../../api/log";
import { SVGIcons } from "../../icons";
import { LogType, STATUS_APP } from "../../types";
export const ReadOnlyTerminal = ({ app }) => {
    const [loading, setLoading] = useState(true);
    const terminal = new Terminal({
        disableStdin: true, // read-only
        fontFamily: 'Monaco, Menlo, Consolas, "Lucida Console", "Courier New", monospace',
        fontSize: 11,
        fontWeight: 400,
        convertEol: true,
        cursorBlink: true,
    });
    const initializeTerminal = () => {
        const containerElement = document.getElementById(`jp-xterm-container-${app.title}`);
        if (containerElement === null) {
            // @TODO: handle this error
            return;
        }
        const fitAddon = new FitAddon();
        terminal.loadAddon(fitAddon);
        terminal.open(containerElement);
        const terminalResizer = new ResizeObserver(() => {
            try {
                fitAddon.fit();
            }
            catch (e) {
                console.log("Unable to fit terminal");
            }
        });
        terminalResizer.observe(containerElement);
    };
    useEffect(() => {
        const fetchOutput = async () => {
            try {
                const domain = app.domains[0].domain;
                let logType;
                switch (app.status) {
                    case STATUS_APP.PUBLISHED:
                        logType = LogType.SERVER_LOG;
                        break;
                    default:
                        logType = LogType.ERROR_LOG;
                }
                const content = await fetchLogs(domain, logType);
                if (content === null) {
                    terminal.write("Could not retrieve logs. Please refresh this page.");
                    return;
                }
                if (content === "") {
                    terminal.write("No logs to display.");
                    return;
                }
                terminal.write(content);
            }
            catch (err) {
                terminal.write("An error has occurred. Please refresh this page.");
            }
            finally {
                setLoading(false);
            }
        };
        initializeTerminal();
        fetchOutput();
    }, []);
    const LoadingIcon = () => {
        return (React.createElement("img", { src: `data:image/svg+xml;base64,${SVGIcons.PUBLISHING_SECOND_VARIATION}`, alt: "icon - publishing", className: "jp-xterm-loading-icon" }));
    };
    return (React.createElement(React.Fragment, null,
        React.createElement("div", { id: `jp-xterm-container-${app.title}`, className: "jp-xterm-container" }),
        loading ? React.createElement(LoadingIcon, null) : null));
};
export default ReadOnlyTerminal;
