import { create } from "zustand";
export const useHeapAnalytics = create((set, get) => ({
    isHeapAnalyticsEnabled: false,
    setHeapAnalytics: (source) => {
        if (source) {
            set({ isHeapAnalyticsEnabled: source });
        }
    },
    registerEvent: (category, data) => {
        const { isHeapAnalyticsEnabled } = get();
        if (isHeapAnalyticsEnabled) {
            window.heap.track(category, data);
        }
    },
}));
