// @TODO this was a hacky way to handle with JL. Remove once we find good approach
import { create } from "zustand";
// Define a custom hook that returns the state and actions
export const useAppShellStateStore = create((set, get) => ({
    // define the actions
    app: null,
    appShell: null,
    commandRegistry: null,
    serviceManager: null,
    _openWidgets: new Map(),
    environment: "development",
    // define the actions
    setApp: (app) => set({ app }),
    setAppShell: (appShell) => set({ appShell }),
    setCommandRegistry: (commandRegistry) => set({ commandRegistry }),
    setServiceManager: (serviceManager) => set({ serviceManager }),
    /**
     * Close the current widget uisng its context
     */
    closeWidgetById: (widgetId) => {
        const { _openWidgets } = get();
        const widget = _openWidgets.get(widgetId);
        if (widget) {
            widget.dispose();
            _openWidgets.delete(widgetId);
        }
        set({ _openWidgets });
    },
    closeAllWidgetsByApp: (app) => {
        const { _openWidgets } = get();
        for (const [key, widget] of _openWidgets) {
            const hyphenIndex = key.indexOf("-");
            const widgetAppElement = key.substring(hyphenIndex + 1).trim();
            if (widget &&
                (widgetAppElement.includes(app.project_id) ||
                    widgetAppElement.includes(app.title))) {
                widget.dispose();
            }
        }
    },
    setEnvironment(environment) {
        set({ environment });
    },
    addWidgetToMainArea: (widget, mode = "split-right") => {
        const { appShell, _openWidgets } = get();
        // Check if the widget is already open
        const existingWidget = _openWidgets.get(widget.id);
        if (appShell) {
            if (existingWidget) {
                // If the existing widget is not the current active one, make it active
                const { getCurrentWidget } = get();
                const currentActiveWidget = getCurrentWidget();
                if (currentActiveWidget && currentActiveWidget !== widget.id) {
                    appShell.activateById(widget.id);
                }
            }
            else {
                // If the widget is not already open, add it to the main area
                appShell.add(widget, "main", {
                    mode,
                });
                appShell.activateById(widget.id);
                _openWidgets.set(widget.id, widget);
                set({ _openWidgets });
            }
        }
    },
    addWidgetToOpenWidgets: (widgetId, widget) => {
        const { _openWidgets } = get();
        _openWidgets.set(widgetId, widget);
        set({ _openWidgets });
    },
    getCurrentWidget() {
        const { _openWidgets } = get();
        return Array.from(_openWidgets.keys())[Array.from(_openWidgets.keys()).length - 1];
    },
}));
