import { menuHelpActions } from "./MenuHelpActions";
/**
 *
 * handlers file is used for the following reasons:
 *
 * - If we need to sort the order of the actions
 * - If we need to add a new type of menu by status we just need to create a new buildFunction
 * - If we need to override the global config.ts to have a specific opiton by status
 */
export const buildHelpMenu = (menuBuilder) => {
    return menuBuilder
        .addItem(menuHelpActions.myLearning, null)
        .addItem({ ...menuHelpActions.buildYourFirstApp }, null)
        .addItem(menuHelpActions.panelDocumentation, null);
};
