import { getUserId } from "./panel";
// Replace with your Appcues account ID
const APPCUES_ACCOUNT_ID = "146413";
/**
 * This function dynamically loads the Appcues script.
 */
export function loadAppcuesScript() {
    console.log("Loading Appcues script..."); // Debug log
    // Create a new script element
    const script = document.createElement("script");
    script.type = "text/javascript";
    script.src = `//fast.appcues.com/${APPCUES_ACCOUNT_ID}.js`;
    // Append the script element to the head of the document
    document.head.appendChild(script);
    // Set up the onload callback for the script
    script.onload = () => {
        console.log("Appcues script loaded."); // Debug log
        // Set the Appcues settings on the window object
        window.AppcuesSettings = {
            enableURLDetection: true,
        };
        // Identify the user to Appcues
        identifyUser();
    };
}
/**
 * This function identifies the user to Appcues.
 */
function identifyUser() {
    console.log("Identifying user..."); // Debug log
    // Fetch the user id
    const user = {
        uid: getUserId(),
    };
    console.log(`User id: ${user.uid}`); // Debug log
    // Check that the Appcues object is available on the window
    if (window && window.Appcues) {
        // Identify the user to Appcues
        window.Appcues.identify(user.uid);
        console.log("User identified to Appcues."); // Debug log
    }
}
