import { create } from "zustand";
export const useFeatureFlags = create((set, get) => ({
    manager: null,
    setFFManager: (manager) => {
        set({ manager });
    },
    evaluateFeatureFlag: (flag, fallback = false) => {
        const { manager } = get();
        return manager ? manager.getBool(flag, fallback) : false;
    },
}));
