import { ReactWidget } from "@jupyterlab/apputils";
import React from "react";
import { LMSApp } from "../../container/LMSApp";
/**
 * A Widget that wraps a DataCatalogs Component.
 */
export class LMSWidget extends ReactWidget {
    app;
    config;
    urlParams;
    /**
     * Constructs a new DataCatalogsWidget.
     */
    constructor(app, config, urlParams) {
        super();
        this.app = app;
        this.config = config;
        this.urlParams = urlParams;
    }
    render() {
        return (React.createElement(LMSApp, { app: this.app, config: this.config, urlParams: this.urlParams }));
    }
}
