import { Dialog } from "@jupyterlab/apputils";
import React from "react";
import { CreateAppWidget } from "../../widgets/CreateAppWidget";
import { ViewAppWidget } from "../../widgets/ViewAppWidget";
function createModalDialog({ options, }) {
    const dialog = new Dialog(options);
    dialog.launch().then(() => {
        // Remove the host element from the DOM after the dialog is closed
        if (options.host) {
            options.host.remove();
        }
    });
    return dialog;
}
function createHostElement(className) {
    const host = document.createElement("div");
    host.classList.add(className);
    document.body.appendChild(host);
    return host;
}
export function showNewAppModal(notebookFilePath, options = {}) {
    const host = createHostElement("anaconda-apps-modal");
    const dialogOptions = {
        ...options,
        title: notebookFilePath,
        body: React.createElement(CreateAppWidget, {
            notebookFilePath,
            closeCreateAppModal: () => {
                dialog.reject();
            },
        }, null),
        hasClose: true,
        buttons: [],
        host,
    };
    const dialog = createModalDialog({ options: dialogOptions });
}
export function showViewAppModal(app, options = {}) {
    const host = createHostElement("anaconda-apps-modal");
    const dialogOptions = {
        ...options,
        title: app.notebook_file_name.split("/").pop() || "",
        body: React.createElement(ViewAppWidget, {
            app,
            closeViewAppModal: () => {
                dialog.reject();
            },
        }, null),
        hasClose: true,
        buttons: [],
        host,
    };
    const dialog = createModalDialog({ options: dialogOptions });
}
