import { backend } from "@anaconda/shared";
import { useEffect, useState } from "react";
import { FetchStatus } from "../types";
export async function fetchNotebookData(notebook_path, retries = 1) {
    while (retries >= 0) {
        try {
            // Try to fetch the notebook data
            const response = await backend.backendFetch(`aext_lms_notebooks_server/notebook?notebook_path=${notebook_path}`);
            return backend.handleProxyResponse(response);
        }
        catch (error) {
            // In case of an error, log it and check if we should retry
            console.error("Error fetching notebook data", error);
            if (retries > 0) {
                // If retries are left, wait for 1 second and then retry
                await new Promise((resolve) => setTimeout(resolve, 1000));
            }
            else {
                // If no retries are left, throw the error
                throw error;
            }
        }
        retries--;
    }
}
export const useLMSContentFetch = () => {
    const [categories, setCategories] = useState([]);
    const [error, setError] = useState(null);
    const [status, setStatus] = useState(FetchStatus.IDLE);
    useEffect(() => {
        setStatus(FetchStatus.LOADING);
        backend
            .backendFetch("aext_lms_notebooks_server/lms-content")
            .then((r) => backend.handleProxyResponse(r))
            .then((response) => {
            setCategories(response);
            setStatus(FetchStatus.SUCCESS);
        })
            .catch((e) => {
            console.error("LMS error", e);
            setError(e);
            setStatus(FetchStatus.ERROR);
        });
    }, []);
    return { categories, error, status };
};
