import React, { useMemo } from "react";
import { CourseTile } from "../Tile/CourseTile";
import { useHeapAnalytics } from "../../store/heap";
export const CourseList = ({ courses, onCourseClick, categorySelected, }) => {
    const heapAnalytics = useHeapAnalytics((state) => ({
        registerEvent: state.registerEvent,
    }));
    const handleClick = useMemo(() => {
        return (course) => {
            heapAnalytics.registerEvent("LMS Notebooks - Course Click", {
                course: course.title,
            });
            onCourseClick(course);
        };
    }, [onCourseClick]);
    return (React.createElement("div", { className: "anaconda-lms-course-list", "data-testid": "course-list" }, courses.map((course, index) => (React.createElement("div", { "data-testid": `course-tile-${course.title}`, key: course.title, onClick: () => handleClick(course) },
        React.createElement(CourseTile, { courseTitle: course.title, categoryTitle: categorySelected ? categorySelected.title : "", courseType: course.course_type, courseIcon: course.course_icon, courseDescription: course.description, notebooksCount: course.notebooks.length }))))));
};
