import { useEffect } from "react";
import { useAppStore } from "../state/apps-zustand/store";
import { STATUS_APP } from "../types";
import { LONG_POLLING_INTERVAL, SHORT_POLLING_INTERVAL } from "../constants";
export const usePolling = (delay) => {
    const { getApps } = useAppStore();
    const tick = async () => {
        await getApps();
    };
    // initial request
    useEffect(() => {
        tick();
    }, []);
    // periodic requests
    useEffect(() => {
        if (delay <= 0) {
            return;
        }
        const id = setInterval(async () => {
            await tick();
        }, delay);
        return () => clearInterval(id);
    }, [delay]);
};
export const getPollingInterval = (apps) => {
    /*  Criteria:
      | 0. if tab is not visible -> no polling;
      | 1. if there are any publishing apps -> polling every 30s;
      | 2. if there are any published apps -> polling every 2min;
      | 3. if there are only inactive apps or no apps -> no polling (requests to do either
      |    action triggers polling).
    */
    const { publishing, published } = apps.reduce((acc, cur) => {
        switch (cur.status) {
            case STATUS_APP.PUBLISHING:
                return { ...acc, publishing: [...acc.publishing, cur] };
            case STATUS_APP.PUBLISHED:
                return { ...acc, published: [...acc.published, cur] };
            default:
                return acc;
        }
    }, {
        publishing: [],
        published: [],
    });
    if (publishing.length !== 0) {
        // 30 seconds
        return SHORT_POLLING_INTERVAL * 1000;
    }
    if (published.length !== 0) {
        // 2 minutes
        return LONG_POLLING_INTERVAL * 1000;
    }
    // no polling for inactive + non-existent apps
    return -1;
};
