import { useReducer, useRef, useEffect } from "react";
import { fetchProject, createApp, saveApp } from "../api/project";
import { APIRequestError } from "../api";
import { useAppStore } from "../state/apps-zustand/store";
var APIRequestType;
(function (APIRequestType) {
    APIRequestType["FETCH"] = "fetch";
    APIRequestType["SAVE"] = "save";
    APIRequestType["CREATE"] = "create";
})(APIRequestType || (APIRequestType = {}));
var APIActionType;
(function (APIActionType) {
    APIActionType["SET_LOADING"] = "SET_LOADING";
    APIActionType["SET_ERROR"] = "SET_ERROR";
    APIActionType["SET_CONTENT"] = "SET_CONTENT";
})(APIActionType || (APIActionType = {}));
const reducer = (state, action) => {
    const { request, value } = action.payload;
    switch (action.type) {
        case APIActionType.SET_LOADING:
            return {
                ...state,
                [request]: {
                    ...state[request],
                    loading: value,
                    // This will reset the previous content and prevent some weird behaviors
                    content: null,
                    error: value ? null : state[request].error,
                },
            };
        case APIActionType.SET_ERROR:
            return {
                ...state,
                [request]: {
                    ...state[request],
                    error: value,
                    content: null,
                },
            };
        case APIActionType.SET_CONTENT:
            return {
                ...state,
                [request]: {
                    ...state[request],
                    content: value,
                    error: null,
                },
            };
        default:
            return state;
    }
};
/**
 *
 * Call the API to handle all app-related requests.
 *
 * @returns array with current api status and action objects
 */
export const useAPI = () => {
    // This is a workaround to prevent state updates on unmounted components and avoid memory leaks
    const isMounted = useRef(true);
    const { getApps } = useAppStore();
    const [state, dispatch] = useReducer(reducer, {
        fetch: {
            loading: false,
            error: null,
            content: null,
        },
        save: {
            loading: false,
            error: null,
            content: null,
        },
        create: {
            loading: false,
            error: null,
            content: null,
        },
    });
    // Wrapper function to prevent state updates on unmounted components
    // The action should be type and payload
    const dispatchAction = (action) => {
        if (isMounted.current) {
            dispatch(action);
        }
    };
    useEffect(() => {
        return () => {
            isMounted.current = false;
        };
    }, []);
    const fetch = async (fileName, appID = "") => {
        dispatchAction({
            type: APIActionType.SET_LOADING,
            payload: { request: APIRequestType.FETCH, value: true },
        });
        try {
            const content = await fetchProject(fileName, appID);
            dispatchAction({
                type: APIActionType.SET_LOADING,
                payload: { request: APIRequestType.FETCH, value: false },
            });
            if (content) {
                dispatchAction({
                    type: APIActionType.SET_CONTENT,
                    payload: { request: APIRequestType.FETCH, value: content },
                });
            }
            return content;
        }
        catch (error) {
            if (error instanceof Error || error instanceof APIRequestError) {
                dispatchAction({
                    type: APIActionType.SET_ERROR,
                    payload: {
                        request: APIRequestType.FETCH,
                        value: error.message,
                    },
                });
            }
            dispatchAction({
                type: APIActionType.SET_LOADING,
                payload: { request: APIRequestType.FETCH, value: false },
            });
            return null;
        }
    };
    const save = async (app) => {
        dispatchAction({
            type: APIActionType.SET_LOADING,
            payload: { request: APIRequestType.SAVE, value: true },
        });
        try {
            const content = await saveApp(app);
            // Ignore error for getApps
            await getApps().catch(() => null);
            dispatchAction({
                type: APIActionType.SET_LOADING,
                payload: { request: APIRequestType.SAVE, value: false },
            });
            if (content) {
                dispatchAction({
                    type: APIActionType.SET_CONTENT,
                    payload: { request: APIRequestType.SAVE, value: content },
                });
            }
            return content;
        }
        catch (error) {
            if (error instanceof Error || error instanceof APIRequestError) {
                dispatchAction({
                    type: APIActionType.SET_ERROR,
                    payload: {
                        request: APIRequestType.SAVE,
                        value: error.message,
                    },
                });
            }
            dispatchAction({
                type: APIActionType.SET_LOADING,
                payload: { request: APIRequestType.SAVE, value: false },
            });
            return null;
        }
    };
    const create = async (appID, command) => {
        dispatchAction({
            type: APIActionType.SET_LOADING,
            payload: { request: APIRequestType.CREATE, value: true },
        });
        try {
            const content = await createApp(appID, command);
            // Ignore error for getApps
            await getApps().catch(() => null);
            dispatchAction({
                type: APIActionType.SET_LOADING,
                payload: { request: APIRequestType.CREATE, value: false },
            });
            if (content) {
                dispatchAction({
                    type: APIActionType.SET_CONTENT,
                    payload: { request: APIRequestType.CREATE, value: content },
                });
            }
            return content;
        }
        catch (error) {
            if (error instanceof Error) {
                dispatchAction({
                    type: APIActionType.SET_ERROR,
                    payload: {
                        request: APIRequestType.CREATE,
                        value: error.message,
                    },
                });
            }
            dispatchAction({
                type: APIActionType.SET_LOADING,
                payload: { request: APIRequestType.CREATE, value: false },
            });
            return null;
        }
    };
    const actions = {
        fetch,
        save,
        create,
    };
    return [state, actions];
};
