import React from "react";
/**
 * Remove all special characters from a string
 *
 * @param str
 * @returns string
 */
export const removeSpecialChars = (str) => {
    if (!str) {
        return "";
    }
    return str.replace(/[^a-zA-Z0-9 ]/g, "");
};
export const getIcon = (icon, alt) => {
    return React.createElement("img", {
        src: `data:image/svg+xml;base64,${icon}`,
        alt: alt,
    });
};
/**
 *  Open a url in a new tab
 *
 * @param url
 */
export const openInNewTab = (url) => {
    if (!url) {
        return;
    }
    window.open(url, "_blank", "noreferrer");
};
/**
 * Convert a base64 string to a blob
 */
export const base64ToBlob = (base64, mimeType) => {
    const byteString = window.atob(base64);
    const arrayBuffer = new ArrayBuffer(byteString.length);
    const int8Array = new Uint8Array(arrayBuffer);
    for (let i = 0; i < byteString.length; i++) {
        int8Array[i] = byteString.charCodeAt(i);
    }
    const blob = new Blob([int8Array], { type: mimeType });
    return blob;
};
/**
 * Should download a file receiving a blob parameter
 *
 * @param blob
 * @param fileName
 */
export const downloadFile = (blob, fileName) => {
    // Other browsers
    // Create a link pointing to the ObjectURL containing the blob
    const blobURL = URL.createObjectURL(blob);
    const tempLink = document.createElement("a");
    tempLink.style.display = "none";
    tempLink.href = blobURL;
    tempLink.setAttribute("download", fileName);
    // Safari thinks _blank anchor are pop ups. We only want to set _blank
    // target if the browser does not support the HTML5 download attribute.
    // This allows you to download files in desktop safari if pop up blocking
    // is enabled.
    if (typeof tempLink.download === "undefined") {
        tempLink.setAttribute("target", "_blank");
    }
    document.body.appendChild(tempLink);
    tempLink.click();
    setTimeout(() => {
        // For Firefox it is necessary to delay revoking the ObjectURL
        document.body.removeChild(tempLink);
        URL.revokeObjectURL(blobURL);
    }, 100);
};
export const transformDate = (dateString) => {
    const [datePart, timepart] = dateString.split(" ");
    const [time] = timepart.split(".");
    return new Date(`${datePart}T${time}`);
};
/**
 * Finds the category to which a given course belongs.
 * @param course The course to find the category for.
 * @param categories An array of categories to search within.
 * @returns The category object containing the given course and category title.
 */
export const findCategorySelected = (course, categories) => {
    for (const category of categories) {
        if (category.courses.includes(course)) {
            return category;
        }
    }
};
