import React from "react";
import { ReactWidget } from "@jupyterlab/apputils";
import { useWidget } from "./hooks/OpenWidgetHook";
import { WidgetType } from "./types";
import { useAppShellStateStore } from "./state/app-shell-zustand/store";
import AppCounter from "./components/AppCounter/AppCounter";
import { configureCommands, configureDocRegistry, configurePalette, loadScripts, } from "./utils/panel";
import { useFeatureFlags } from "./state/apps-zustand/feature-flags";
import { useTheme } from "./hooks/theme";
import { useHeapAnalytics } from "./state/apps-zustand/heap";
import { useCloudApiUrl } from "@anaconda/lms_notebooks_lib";
export default class PanelPlugin {
    async activate(app, opts) {
        const coreReady = await opts.corePlugin.ready();
        if (!coreReady.featureFlag.getBool("monorepo-panel", false)) {
            return;
        }
        const hidePublishButton = !coreReady.featureFlag.getBool("monorepo-panel-show-publish-button", false);
        console.log("[Monorepo] Enabling the panel plugin");
        const { setApp, setAppShell, setCommandRegistry, setServiceManager } = useAppShellStateStore.getState();
        setApp(app);
        setAppShell(app.shell);
        setCommandRegistry(app.commands);
        setServiceManager(app.serviceManager);
        const { setFFManager } = useFeatureFlags.getState();
        setFFManager(coreReady.featureFlag);
        // initialize heap analytics store
        const { setHeapAnalytics } = useHeapAnalytics.getState();
        setHeapAnalytics(coreReady.analytics.heap);
        const { setApiUrl } = useCloudApiUrl.getState();
        setApiUrl(coreReady.config.cloud.url);
        loadScripts();
        configurePalette(opts.palette);
        configureDocRegistry(app, hidePublishButton);
        configureCommands(app, opts.notebookTracker);
        // initialize theme manager
        const { setThemeManager } = useTheme.getState();
        setThemeManager(opts.themeManager);
        const panelMyAppLauncherCommand = "anaconda-panel-launcher:open";
        const panelMyAppLauncherCategory = "Anaconda";
        const panelMyAppLauncherLabel = "My Apps";
        app.commands.addCommand(panelMyAppLauncherCommand, {
            label: panelMyAppLauncherLabel,
            iconClass: "anaconda-logo-icon",
            execute: async () => {
                try {
                    const { openWidget } = useWidget();
                    // Activating the widget
                    openWidget(WidgetType.APP_MANAGEMENT);
                }
                catch (err) {
                    //@TODO: Implement Datadog alerts
                    console.error("Panel - error", err);
                }
            },
        });
        opts.toolbarRegistry.registerFactory("TopBar", "panel-apps-counter", () => {
            return ReactWidget.create(React.createElement(AppCounter));
        });
        // Adding the command to the launcher
        opts.launcher.add({
            command: panelMyAppLauncherCommand,
            category: panelMyAppLauncherCategory,
        });
    }
}
