import { Dialog } from "@jupyterlab/apputils";
import React from "react";
import { AppVersionsView } from "../../views/AppVersionsView";
function createModalDialog({ options, }) {
    const dialog = new Dialog(options);
    dialog.launch().then(() => {
        // Remove the host element from the DOM after the dialog is closed
        if (options.host) {
            options.host.remove();
        }
    });
    return dialog;
}
function createHostElement(className) {
    const host = document.createElement("div");
    host.classList.add(className);
    document.body.appendChild(host);
    return host;
}
export function showVersionsModal(app, options = {}) {
    const host = createHostElement("anaconda-versions-modal");
    const dialogOptions = {
        ...options,
        title: "App Versions",
        body: React.createElement(AppVersionsView, {
            app,
            closeModal: () => {
                dialog.reject();
            },
        }, null),
        hasClose: true,
        buttons: [],
        host,
    };
    const dialog = createModalDialog({ options: dialogOptions });
}
