import { APIRequestError, requestAPI } from "../api";
import { App, FeatureFlag, } from "../types";
import { useFeatureFlags } from "../state/apps-zustand/feature-flags";
import { backend } from "@anaconda/shared";
export async function fetchProject(notebook_file_path, project_id) {
    const parameters = new URLSearchParams({
        notebook_file_path,
    });
    if (project_id) {
        parameters.append("project_id", project_id);
    }
    try {
        const res = await requestAPI(`project-info?${parameters}`, {
            headers: getCloudStorageFFHeader(),
        });
        if (res.meta.error.status && !res.meta.error.message) {
            throw new APIRequestError("An error occurred while retrieving project information");
        }
        else if (res.meta.error.status && res.meta.error.message) {
            throw new APIRequestError(res.meta.error.message);
        }
        return res.content;
    }
    catch (e) {
        if (e instanceof APIRequestError) {
            throw new APIRequestError(e.message);
        }
        throw new APIRequestError("Could not communicate with API endpoint");
    }
}
export async function saveApp(app) {
    const { project_id, notebook_file_name: path, title, versioning, description, environment, } = app;
    const init = {
        method: "POST",
        body: new URLSearchParams({
            project_id,
            path,
            title,
            description,
            version: versioning.next_version,
            environment,
        }),
        headers: getCloudStorageFFHeader(),
    };
    try {
        const res = await requestAPI("projects", init);
        if (res.meta.error.status && !res.meta.error.message) {
            throw new APIRequestError(`An error occurred while saving ${path}`);
        }
        else if (res.meta.error.status && res.meta.error.message) {
            throw new APIRequestError(res.meta.error.message);
        }
        return res.content;
    }
    catch (e) {
        if (e instanceof APIRequestError) {
            throw new APIRequestError(e.message);
        }
        throw new APIRequestError("Could not communicate with API endpoint");
    }
}
export async function fetchApps() {
    try {
        const res = (await requestAPI("deployments", { headers: getCloudStorageFFHeader() })) || [];
        if (res.content && Array.isArray(res.content.apps)) {
            return {
                ...res.content,
                apps: res.content.apps.map((app) => {
                    return new App(app);
                }),
            };
        }
        else if (res.meta.error.status && res.meta.error.message) {
            throw new APIRequestError(res.meta.error.message);
        }
        throw new APIRequestError("An error occurred while retrieving project information");
    }
    catch (e) {
        if (e instanceof APIRequestError) {
            throw new APIRequestError(e.message);
        }
        throw new APIRequestError("Could not communicate with API endpoint");
    }
}
export async function fetchFeaturedApps() {
    try {
        const response = await backend.backendFetch("aext_panels_server/featured-apps", {}, {});
        if (response.remote_status_code != 200) {
            throw new Error();
        }
        return response.remote_data;
    }
    catch (err) {
        console.error(`Error retrieving featured apps`);
    }
}
