import { ToolbarButton } from "@jupyterlab/apputils";
import tippy from "tippy.js";
import "tippy.js/dist/tippy.css";
import { showNewAppModal } from "../components/AppModal/AppModal";
import { publishGreenIcon } from "../icons";
import { useAppShellStateStore } from "../state/app-shell-zustand/store";
import { loadAppcuesScript } from "./app-cues";
export var CommandIDs;
(function (CommandIDs) {
    CommandIDs.paDeploy = "notebook:deploy-to-pa";
})(CommandIDs || (CommandIDs = {}));
/**
 * A notebook widget extension that adds a PA deploy button to the toolbar.
 */
export class PADeployButton {
    /**
     * Instantiate a new PADeployButton.
     * @param commands The command registry.
     */
    constructor(commands, app) {
        this._commands = commands;
    }
    /**
     * Create a new extension object.
     */
    createNew(panel) {
        const button = new ToolbarButton({
            className: "paDeploy",
            icon: publishGreenIcon,
            onClick: () => {
                this._commands.execute(CommandIDs.paDeploy);
            },
        });
        setTimeout(() => {
            requestAnimationFrame(() => {
                tippy(button.node, {
                    arrow: true,
                    content: "Save and publish",
                    placement: "bottom",
                });
            });
        }, 0);
        panel.toolbar.insertAfter("cellType", "paDeploy", button);
        return button;
    }
    _commands;
}
export function configureSettingRegistry(plugin, settingRegistry) {
    if (settingRegistry) {
        settingRegistry
            .load(plugin.id)
            .then((settings) => {
            console.log("PythonAnywhere extension settings loaded:", settings.composite);
        })
            .catch((reason) => {
            console.error("Failed to load settings for PythonAnywhere extension.", reason);
        });
    }
}
export function configurePalette(palette) {
    if (palette) {
        const category = "Notebook Operations";
        palette.addItem({ command: CommandIDs.paDeploy, category });
    }
}
export function configureDocRegistry(app, hidePublishButton) {
    if (hidePublishButton) {
        return;
    }
    const deployButton = new PADeployButton(app.commands, app);
    app.docRegistry.addWidgetExtension("Notebook", deployButton);
    app.docRegistry.addWidgetExtension("Editor", deployButton);
}
export function getCurrentNotebook(app, notebooks, args) {
    const { addWidgetToOpenWidgets } = useAppShellStateStore.getState();
    const widget = notebooks.currentWidget;
    const activate = args["activate"] !== false;
    console.log(notebooks.currentWidget, "test");
    if (activate && widget) {
        const notebookFilePath = widget.context.path.replace(".ipynb", "") || "";
        const formWidgetId = `create-app-${notebookFilePath}`;
        addWidgetToOpenWidgets(formWidgetId, widget);
        app.shell.activateById(widget.id);
    }
    return widget;
}
export function configureCommands(app, notebooks) {
    app.commands.addCommand(CommandIDs.paDeploy, {
        label: "Deploy notebook to Python Anywhere",
        execute: async (args) => {
            const current = getCurrentNotebook(app, notebooks, args);
            let context;
            console.log("test", current);
            if (current) {
                context = current.context;
                // save notebook file automatically when user click on PA-deploy button
                await context.save();
                showNewAppModal(current.context.path);
            }
        },
        isEnabled: () => isWidgetEnabled(app, notebooks),
    });
}
export function isWidgetEnabled(app, notebooks) {
    const widget = app.shell.currentWidget;
    if (widget?.context === undefined) {
        return false;
    }
    return (widget.context.path.endsWith(".py") ||
        (notebooks.currentWidget !== null && notebooks.currentWidget === widget));
}
export function getUserId() {
    let userId = "unknown-user-id";
    const urlPathName = document.location.pathname;
    const regExpMatch = urlPathName.match(/\/user\/([^/]*)\//i);
    if (regExpMatch && regExpMatch.length > 0) {
        userId = regExpMatch[1];
    }
    return userId;
}
export function loadScripts() {
    try {
        loadAppcuesScript();
    }
    catch (error) {
        console.error("Error starting AppCues tracking: ", error);
    }
}
