import React from "react";
import { useHeapAnalytics } from "../../store/heap";
import { openNotebook } from "../../utils";
export const NotebookTile = ({ app, notebook, courseSelected, categorySelected, }) => {
    const heapAnalytics = useHeapAnalytics((state) => ({
        registerEvent: state.registerEvent,
    }));
    const openNotebookHandler = (app, notebook) => {
        heapAnalytics.registerEvent("LMS Notebooks - Notebook Click", {
            category: notebook.title,
        });
        openNotebook({
            app,
            notebook_title: notebook.title,
            notebook_path: notebook.notebook_path,
            path: `anaconda-learning/${categorySelected?.title}/${courseSelected?.title}`,
        }).catch((err) => console.error(err));
    };
    // @TODO refactor component using compound components
    return (React.createElement("div", { className: "anaconda-lms-nb-tile" },
        React.createElement("div", { className: "anaconda-lms-nb-frame" },
            React.createElement("div", { className: "anaconda-lms-nb-gradient" }),
            React.createElement("div", { className: "anaconda-lms-nb-content" },
                React.createElement("div", { className: "anaconda-lms-nb-title-container" },
                    React.createElement("div", { className: "anaconda-lms-nb-title-div" },
                        React.createElement("div", { className: "anaconda-lms-notebook-icon" }),
                        React.createElement("div", { className: "anaconda-lms-nb-title-text" },
                            notebook.title,
                            ".ipynb"))),
                React.createElement("div", { className: "anaconda-lms-nb-download-div" },
                    React.createElement("div", { "data-testid": `notebook-tile-${notebook.title}`, onClick: () => {
                            openNotebookHandler(app, notebook);
                        }, className: "anaconda-lms-nb-download-text" }, "Open in new notebook"),
                    React.createElement("div", { className: "anaconda-lms-chevron-right-icon" }))))));
};
