import { create } from "zustand";
import { createApp, deleteApp, deleteAppVersion, fetchApps, fetchFeaturedApps, publishApp, restartApp, unpublishApp, } from "../../api/project";
export var STATUS_REQUEST;
(function (STATUS_REQUEST) {
    STATUS_REQUEST["IDLE"] = "IDLE";
    STATUS_REQUEST["INITIAL_LOADING"] = "INITIAL_LOADING";
    STATUS_REQUEST["REFRESHING"] = "REFRESHING";
    STATUS_REQUEST["SUCCESS"] = "SUCCESS";
    STATUS_REQUEST["ERROR"] = "ERROR";
})(STATUS_REQUEST || (STATUS_REQUEST = {}));
const INITIAL_STATE = {
    apps: [],
    featuredApps: [],
    maxApps: 0,
    appsDeployed: 0,
    canPublish: true,
    // @TODO refactor statusRequest to be a hashMap of all apps requests status (unpublish, delete, ...)
    statusRequest: STATUS_REQUEST.IDLE,
    getAppStatusRequest: STATUS_REQUEST.IDLE,
    error: null,
};
// Define a custom hook that returns the state and actions
export const useAppStore = create((set, get) => ({
    //define the initial state
    ...INITIAL_STATE,
    // define the actions
    getApps: async () => {
        try {
            // Set the INITIAL_LOADING only the first time
            if (get().getAppStatusRequest === STATUS_REQUEST.IDLE) {
                set({ getAppStatusRequest: STATUS_REQUEST.INITIAL_LOADING });
            }
            else {
                set({ getAppStatusRequest: STATUS_REQUEST.REFRESHING });
            }
            const content = await fetchApps();
            set({
                getAppStatusRequest: STATUS_REQUEST.SUCCESS,
                apps: content.apps,
                maxApps: content.num_max_apps,
                appsDeployed: content.num_active_apps,
                canPublish: content.num_active_apps < content.num_max_apps ||
                    // If the API brings a temporary error we will enable the publish either way
                    content.num_max_apps === 0,
            });
        }
        catch (error) {
            // Show error screen only the first time or when the user refresh the page and has error
            if (STATUS_REQUEST.INITIAL_LOADING === get().getAppStatusRequest ||
                STATUS_REQUEST.IDLE === get().getAppStatusRequest) {
                if (error instanceof Error) {
                    set({
                        getAppStatusRequest: STATUS_REQUEST.ERROR,
                        error: error.message,
                    });
                }
                else {
                    set({
                        statusRequest: STATUS_REQUEST.ERROR,
                        error: "Something went wrong",
                    });
                }
            }
        }
    },
    getFeaturedApps: async () => {
        try {
            const content = await fetchFeaturedApps();
            set({
                featuredApps: content,
            });
        }
        catch (error) {
            if (error instanceof Error) {
                set({ statusRequest: STATUS_REQUEST.ERROR, error: error.message });
            }
            else {
                set({
                    statusRequest: STATUS_REQUEST.ERROR,
                    error: "Something went wrong",
                });
            }
        }
    },
    createApp: async (app_id, command) => {
        try {
            const domain = await createApp(app_id, command);
            const { getApps } = get();
            await getApps();
            return domain?.app || null;
        }
        catch (error) {
            if (error instanceof Error) {
                set({ statusRequest: STATUS_REQUEST.ERROR, error: error.message });
            }
            else {
                set({
                    statusRequest: STATUS_REQUEST.ERROR,
                    error: "Something went wrong",
                });
            }
            return null;
        }
    },
    deleteApp: async (projectId, domainName) => {
        try {
            await deleteApp(projectId, domainName);
            const { getApps } = get();
            await getApps();
            return true;
        }
        catch (error) {
            if (error instanceof Error) {
                set({ statusRequest: STATUS_REQUEST.ERROR, error: error.message });
            }
            else {
                set({
                    statusRequest: STATUS_REQUEST.ERROR,
                    error: "Something went wrong",
                });
            }
            return false;
        }
    },
    restartApp: async (domainName) => {
        try {
            const domain = await restartApp(domainName);
            const { getApps } = get();
            await getApps();
            return domain?.app || null;
        }
        catch (error) {
            if (error instanceof Error) {
                set({ statusRequest: STATUS_REQUEST.ERROR, error: error.message });
            }
            else {
                set({
                    statusRequest: STATUS_REQUEST.ERROR,
                    error: "Something went wrong",
                });
            }
            return null;
        }
    },
    publishApp: async (domainName) => {
        try {
            const domain = await publishApp(domainName);
            const { getApps } = get();
            await getApps();
            return domain?.app || null;
        }
        catch (error) {
            if (error instanceof Error) {
                set({ statusRequest: STATUS_REQUEST.ERROR, error: error.message });
            }
            else {
                set({
                    statusRequest: STATUS_REQUEST.ERROR,
                    error: "Something went wrong",
                });
            }
            return null;
        }
    },
    unpublishApp: async (domainName) => {
        try {
            await unpublishApp(domainName);
            const { getApps } = get();
            await getApps();
            return true;
        }
        catch (error) {
            if (error instanceof Error) {
                set({ statusRequest: STATUS_REQUEST.ERROR, error: error.message });
            }
            else {
                set({
                    statusRequest: STATUS_REQUEST.ERROR,
                    error: "Something went wrong",
                });
            }
            return false;
        }
    },
    deleteAppVersion: async (projectId, versionId) => {
        try {
            await deleteAppVersion(projectId, versionId);
            const { getApps } = get();
            await getApps();
            return true;
        }
        catch (error) {
            if (error instanceof Error) {
                set({ statusRequest: STATUS_REQUEST.ERROR, error: error.message });
            }
            else {
                set({
                    statusRequest: STATUS_REQUEST.ERROR,
                    error: "Something went wrong",
                });
            }
            return false;
        }
    },
    checkAppDuplicated: (title, projectId = "") => {
        const { apps } = get();
        const isDuplicated = apps.some((app) => app.title === title && app.project_id !== projectId);
        return isDuplicated;
    },
    resetState: () => {
        set(INITIAL_STATE);
    },
    getAppByProjectId: (projectId) => {
        const { apps } = get();
        return apps.find((app) => app.project_id === projectId) || null;
    },
}));
