import { MAX_PRO_PLUS_SUBSCRIPTIONS } from "../../../constants";
import { useAppStore } from "../../../state/apps-zustand/store";
import { FeatureFlag } from "../../../types";
import { menuAppActions } from "./MenuAppActions";
import { useFeatureFlags } from "../../../state/apps-zustand/feature-flags";
/**
 *
 * handlers file is used for the following reasons:
 *
 * - If we need to sort the order of the actions
 * - If we need to add a new type of menu by status we just need to create a new buildFunction
 * - If we need to override the global config.ts to have a specific opiton by status
 */
export const buildFailedMenu = (menuBuilder, app) => {
    const store = useFeatureFlags.getState();
    const isCloudStorageFFEnabled = store.evaluateFeatureFlag(FeatureFlag.CLOUD_STORAGE);
    menuBuilder
        .addItem({ ...menuAppActions.viewInNewTab, disabled: true }, app)
        .addItem({ ...menuAppActions.viewInJupyterLab, disabled: true }, app)
        .addLineSeparator()
        .addItem(menuAppActions.restart, app)
        .addItem({ ...menuAppActions.unpublish, disabled: true }, app)
        .addLineSeparator()
        .addItem({ ...menuAppActions.copyAppLink, disabled: true }, app)
        .addItem({ ...menuAppActions.viewErrorLogs }, app)
        .addLineSeparator()
        .addItem(menuAppActions.viewConfiguration, app);
    if (isCloudStorageFFEnabled) {
        menuBuilder.addItem(menuAppActions.viewVersions, app);
    }
    menuBuilder
        .addLineSeparator()
        .addItem(menuAppActions.download, app)
        .addLineSeparator()
        .addItem(menuAppActions.delete, app);
    return menuBuilder.addItem(menuAppActions.lastOptionMessage, app);
};
