export const buildInactiveMenu = (menuBuilder, app) => {
    const { canPublish, appsDeployed } = useAppStore.getState();
    // Validate if the user can publish an app based on their subscription
    const publishOption = canPublish
        ? menuAppActions.publish
        : menuAppActions.upgradeToPublish;
    // Should not show the publish option if the user has reached the limit of apps deployed for PRO+ users
    if (appsDeployed < MAX_PRO_PLUS_SUBSCRIPTIONS || canPublish) {
        menuBuilder.addItem(publishOption, app);
    }
    const store = useFeatureFlags.getState();
    const isCloudStorageFFEnabled = store.evaluateFeatureFlag(FeatureFlag.CLOUD_STORAGE);
    if (isCloudStorageFFEnabled) {
        menuBuilder.addItem(menuAppActions.viewVersions, app);
    }
    menuBuilder
        .addItem(menuAppActions.download, app)
        .addLineSeparator()
        .addItem(menuAppActions.delete, app);
    return menuBuilder.addItem(menuAppActions.lastOptionMessage, app);
};
