import { openInNewTab } from "../../../utils";
import { buildYourFirstAppUrl, myLearningUrl, panelDocumentationUrl, } from "./MenuHelpUrls";
/**
 * This is a global config object used for all the menus we have, since a specific option is the same when appear in some menu,
 * we have created this method to make easier to build the menus.
 *
 * If we need to override some specific option by a specific status we can do this on the
 * handlers file
 */
export const menuHelpActions = {
    myLearning: {
        label: "My Learning",
        command: "help:my-learning",
        execute: () => {
            openInNewTab(myLearningUrl);
        },
    },
    buildYourFirstApp: {
        label: "Building Your First App",
        command: "help:build-your-first-app",
        execute: () => {
            openInNewTab(buildYourFirstAppUrl);
        },
    },
    panelDocumentation: {
        label: "Panel Documentation",
        command: "help:panel-documentation",
        execute: () => {
            openInNewTab(panelDocumentationUrl);
        },
    },
};
