import { showErrorMessage } from "@jupyterlab/apputils";
import { backend } from "@anaconda/shared";
async function fetchNotebook(url) {
    const fetchedNotebook = await backend
        .backendFetch(`aext_notebook_load_server/fetch_notebook?nb_url=${encodeURIComponent(url)}`)
        .then((r) => backend.handleProxyResponse(r));
    return fetchedNotebook;
}
export async function loadNotebookFromURL(app, nb_url) {
    let fetchedNotebookResponse;
    try {
        fetchedNotebookResponse = await fetchNotebook(nb_url);
    }
    catch (error) {
        let message = "The notebook could not be fetched";
        if (error instanceof Error) {
            message = error.message;
        }
        console.error(error);
        return showErrorMessage("Notebook fetch failed", message);
    }
    const notebook = await app.commands.execute("notebook:create-new");
    await notebook.context.ready;
    try {
        notebook.context.model.fromJSON(fetchedNotebookResponse.content);
    }
    catch (error) {
        console.error(error);
        return showErrorMessage("Invalid contents", "The URL did not contain valid notebook JSON");
    }
}
