import React, { useMemo } from "react";
import { Theme, WidgetType } from "../types";
import { AgGridReact } from "ag-grid-react";
import { useTheme } from "../hooks/theme";
import ActionCellRenderer from "../components/VersionsModal/ActionCellRenderer";
import { useAppStore } from "../state/apps-zustand/store";
import { downloadAppVersion, getAppVersion } from "../api/project";
import { base64ToBlob, downloadFile, transformDate } from "../utils";
import VersionCellRenderer from "../components/VersionsModal/VersionCellRenderer";
import { useWidget } from "../hooks/OpenWidgetHook";
import { INotification } from "jupyterlab_toastify";
import { useAppShellStateStore } from "../state/app-shell-zustand/store";
import { format } from "date-fns";
export const AppVersionsView = ({ app, closeModal, }) => {
    const { deleteAppVersion } = useAppStore.getState();
    const { theme } = useTheme((state) => ({
        theme: state.theme,
    }));
    const defaultColDef = useMemo(() => {
        return {
            flex: 1,
            minWidth: 100,
            resizable: true,
        };
    }, []);
    const columnDefs = [
        {
            field: "version",
            sortable: true,
            headerName: "Version",
            valueGetter: (params) => {
                return params.data;
            },
            cellRenderer: VersionCellRenderer,
        },
        {
            field: "created_at",
            headerName: "Created",
            flex: 0.7,
            valueFormatter: (params) => {
                if (params.value) {
                    return format(transformDate(params.value), "MMM d, yyyy");
                }
                return "";
            },
        },
        {
            field: "updated_at",
            headerName: "Updated",
            flex: 0.7,
            valueFormatter: (params) => {
                if (params.value) {
                    return format(transformDate(params.value), "MMM d, yyyy");
                }
                return "";
            },
        },
        {
            field: "last_published_at",
            headerName: "Last Published",
            flex: 0.7,
            valueFormatter: (params) => {
                if (params.value) {
                    return format(transformDate(params.value), "MMM d, yyyy");
                }
                return "";
            },
        },
        // {
        //   headerName: 'Published',
        //   flex: 0.7,
        //   valueGetter: (params: ValueGetterParams) => {
        //     return {
        //       data: params.data,
        //       handleRadioChange,
        //     }
        //   },
        //   cellRenderer: PublishedCellRenderer
        // },
        {
            headerName: "Actions",
            valueGetter: (params) => {
                return {
                    data: params.data,
                    handleDownload,
                    handleDelete,
                    handleOpenPublishedApp,
                    handleCopyUrl,
                    handleDuplicate,
                };
            },
            cellRenderer: ActionCellRenderer,
        },
    ];
    const handleDownload = async (data) => {
        if (app) {
            const fileName = `${app?.title}-${data.version}.zip`;
            const mimeType = "application/zip";
            const appDownloaded = await downloadAppVersion(app.project_id, data.id);
            const blob = base64ToBlob(appDownloaded, mimeType);
            downloadFile(blob, fileName);
        }
    };
    const handleDuplicate = async (data) => {
        if (app) {
            const appVersionFiles = await getAppVersion(app.project_id, data.id);
            const { commandRegistry } = useAppShellStateStore.getState();
            const notebook = await commandRegistry.execute("notebook:create-new");
            await notebook.context.ready;
            try {
                notebook.context.model.fromJSON(appVersionFiles.notebook);
            }
            catch (error) {
                console.error(error);
                await INotification.error("Failed to duplicate a notebook", {
                    autoClose: 5000,
                });
            }
        }
    };
    const handleOpenPublishedApp = () => {
        closeModal();
        const { openWidget } = useWidget();
        if (app)
            openWidget(WidgetType.DEPLOYMENT, app);
    };
    // const handleRadioChange = (data: any) => {
    //   // @TODO Add functionality for handling radio change
    // };
    const handleCopyUrl = () => {
        navigator.clipboard
            .writeText(app?.domainUrl)
            .then(() => {
            INotification.success("Copied to clipboard", { autoClose: 5000 });
        })
            .catch((error) => {
            INotification.error("Failed to copy to clipboard", { autoClose: 5000 });
            console.error("Failed to copy to clipboard", error);
        });
    };
    const handleDelete = async (data) => {
        if (app)
            await deleteAppVersion(app.project_id, data.id);
    };
    return (React.createElement("div", { className: theme === Theme.DARK
            ? "anaconda-versions-modal__grid-container ag-theme-alpine-dark"
            : "anaconda-versions-modal__grid-container ag-theme-alpine" },
        React.createElement(AgGridReact, { columnDefs: columnDefs, rowData: app?.versions, rowSelection: "single", rowHeight: 60, defaultColDef: defaultColDef, suppressRowClickSelection: true, enableBrowserTooltips: true })));
};
