import { create } from "zustand";
import { Theme } from "../types";
export const useTheme = create((set, get) => ({
    theme: Theme.LIGHT,
    manager: null,
    setThemeManager: (manager) => {
        set({ manager });
        // Initializes the theme state using default theme of Jupyterlab
        const { setTheme } = get();
        setTheme();
        manager.themeChanged.connect((_, event) => {
            setTheme();
        });
    },
    setTheme: () => {
        const { manager } = get();
        if (manager !== null) {
            const theme = manager.theme && manager.isLight(manager.theme)
                ? Theme.LIGHT
                : Theme.DARK;
            set({ theme });
        }
    },
}));
