import { useHeapAnalytics } from "../../../state/apps-zustand/heap";
/**
 * This is a global config object used for all the menus we have, since a specific option is the same when appear in some menu,
 * we have created this method to make easier to build the menus.
 *
 * If we need to override some specific option by a specific status we can do this on the
 * handlers file
 */
export const menuVersionActions = {
    deleteVersion: {
        label: "Delete Version",
        command: "version:delete",
        execute: async (menuVersionPayload) => {
            const { registerEvent } = useHeapAnalytics.getState();
            registerEvent("nb.anaconda.cloud - Panel - Click - Delete Version");
            if (menuVersionPayload) {
                menuVersionPayload.handleDelete(menuVersionPayload.data);
            }
        },
    },
    downloadApp: {
        label: "Download App",
        command: "version:download",
        execute: async (menuVersionPayload) => {
            const { registerEvent } = useHeapAnalytics.getState();
            registerEvent("nb.anaconda.cloud - Panel - Click - Download App");
            menuVersionPayload.handleDownload(menuVersionPayload.data);
        },
    },
    duplicateNotebook: {
        label: "Duplicate Notebook",
        command: "version:duplicateNotebook",
        execute: async (menuVersionPayload) => {
            const { registerEvent } = useHeapAnalytics.getState();
            registerEvent("nb.anaconda.cloud - Panel - Click - Duplicate Notebook");
            menuVersionPayload.handleDuplicate(menuVersionPayload.data);
        },
    },
    copyUrl: {
        label: "Copy URL",
        command: "version:copyUrl",
        execute: async (menuVersionPayload) => {
            const { registerEvent } = useHeapAnalytics.getState();
            registerEvent("nb.anaconda.cloud - Panel - Click - Copy URL");
            menuVersionPayload.handleCopyUrl();
        },
    },
};
