import { useCallback, useEffect, useReducer } from "react";
import { useLMSContentFetch } from "../api";
import { FetchStatus, View, } from "../types";
import { findCategoryByTitle, findCourseByTitle, findNotebookByTitle, openNotebook as openNotebookHandler, } from "../utils";
const initialLMSState = {
    data: {
        categories: [],
        courses: [],
        notebooks: [],
    },
    currentView: View.CATEGORIES,
    categorySelected: null,
    courseSelected: null,
    querySearch: "",
};
const reducer = (state, action) => {
    switch (action.type) {
        case "SET_QUERY":
            return { ...state, querySearch: action.query };
        case "SET_DATA":
            return { ...state, data: { ...state.data, ...action.data } };
        case "SET_VIEW":
            return { ...state, currentView: action.view };
        case "SET_SELECTED_CATEGORY":
            return { ...state, categorySelected: action.category };
        case "SET_SELECTED_COURSE":
            return { ...state, courseSelected: action.course };
        case "RESET_STATE":
            return {
                ...initialLMSState,
                data: {
                    ...initialLMSState.data,
                    categories: state.data.categories,
                },
            };
        default:
            return state;
    }
};
export const useLMSAppState = (urlParams, app, heapAnalytics, initialState = initialLMSState) => {
    const [state, dispatch] = useReducer(reducer, initialState);
    const { categories, status } = useLMSContentFetch(); // Encapsulated inside the hook
    const openNotebook = useCallback((urlParams, categories, app) => {
        // Find the category
        const category = findCategoryByTitle(categories, urlParams.categoryTitle || "");
        if (!category) {
            // If the category is not found, show the No Results view
            setView(View.NO_RESULTS);
            return;
        } // Find the course
        const course = findCourseByTitle(category, urlParams.courseTitle || "");
        if (!course) {
            // If the course is not found, show the No Results view
            setView(View.NO_RESULTS);
            return;
        } // Find the notebook
        const notebook = findNotebookByTitle(course, urlParams.notebookTitle || "");
        if (!notebook) {
            // If the notebook is not found, show the No Results view
            setView(View.NO_RESULTS);
            return;
        }
        try {
            heapAnalytics.registerEvent("LMS Notebooks - Open notebook from URL", {
                category: `open_lms?${category.title}/${course.title}/${notebook.title}`,
            });
        }
        catch (err) {
            console.error("LMS openNotebook error", err);
        }
        setCourses(category.courses, category);
        setNotebooks(course);
        openNotebookHandler({
            app,
            notebook_title: notebook.title,
            notebook_path: notebook.notebook_path,
            path: `anaconda-learning/${category?.title}/${course?.title}`,
        }).catch((err) => console.error(err));
        return;
    }, []);
    const openNotebookList = useCallback((urlParams, categories) => {
        // Find the category
        const category = findCategoryByTitle(categories, urlParams.categoryTitle || "");
        // Find the course
        if (category) {
            const course = findCourseByTitle(category, urlParams.courseTitle || "");
            // Set the notebook list
            if (course) {
                setCourses(category.courses, category);
                setNotebooks(course);
                try {
                    heapAnalytics.registerEvent("LMS Notebooks - Open notebook list from course URL", {
                        category: `open_lms?${category.title}/${course.title}`,
                    });
                }
                catch (err) {
                    console.error("LMS openNotebook error", err);
                }
                return;
            }
        }
        // If the course or category is not found, show the No Results view
        setView(View.NO_RESULTS);
    }, []);
    const openCourseList = useCallback((urlParams, categories) => {
        const category = findCategoryByTitle(categories, urlParams.categoryTitle || "");
        if (category) {
            setCourses(category.courses, category);
            try {
                heapAnalytics.registerEvent("LMS Notebooks - Open courses list from category URL", {
                    category: `open_lms?${category.title}`,
                });
            }
            catch (err) {
                console.error("LMS openNotebook error", err);
            }
        }
        else {
            // If the category is not found, show the No Results view
            setView(View.NO_RESULTS);
        }
    }, []);
    useEffect(() => {
        console.log("LMS useLMSAppState", { urlParams });
        if (status === FetchStatus.ERROR) {
            dispatch({ type: "SET_VIEW", view: View.ERROR });
        }
        else if (status === FetchStatus.SUCCESS) {
            setCategories(categories);
            if (urlParams) {
                const { categoryTitle, courseTitle, notebookTitle, openLMS } = urlParams;
                if (categoryTitle && courseTitle && notebookTitle) {
                    openNotebook(urlParams, categories, app);
                }
                else if (categoryTitle && courseTitle) {
                    openNotebookList(urlParams, categories);
                }
                else if (categoryTitle) {
                    openCourseList(urlParams, categories);
                }
                else if (openLMS) {
                    heapAnalytics.registerEvent("LMS Notebooks - Open LMS extension with URL", {
                        category: `open_lms`,
                    });
                }
            }
            if (categories.length === 0) {
                setView(View.NO_RESULTS);
            }
        }
    }, [categories, status, urlParams]);
    const setView = useCallback((view) => {
        dispatch({ type: "SET_VIEW", view });
        if (view === View.CATEGORIES) {
            // Perform a clean up
            dispatch({ type: "RESET_STATE" });
        }
    }, []);
    const setQuery = useCallback((query) => {
        dispatch({ type: "SET_QUERY", query });
    }, []);
    const setCategories = useCallback((categories) => {
        dispatch({ type: "SET_DATA", data: { categories } });
    }, []);
    const setCourses = useCallback((courses, categorySelected = null) => {
        dispatch({ type: "SET_DATA", data: { courses: courses } });
        if (categorySelected) {
            dispatch({ type: "SET_SELECTED_CATEGORY", category: categorySelected });
        }
        dispatch({ type: "SET_VIEW", view: View.COURSES });
    }, []);
    const setSelectedCategory = useCallback((categorySelected = null) => {
        dispatch({ type: "SET_SELECTED_CATEGORY", category: categorySelected });
    }, []);
    const setNotebooks = useCallback((courseSelected) => {
        dispatch({
            type: "SET_DATA",
            data: { notebooks: courseSelected.notebooks },
        });
        dispatch({ type: "SET_SELECTED_COURSE", course: courseSelected });
        dispatch({ type: "SET_VIEW", view: View.NOTEBOOKS });
    }, []);
    return {
        state,
        fetchStatus: status,
        setView,
        setQuery,
        setCategories,
        setCourses,
        setSelectedCategory,
        setNotebooks,
        openNotebook,
    };
};
