/**
 * This class represent the apps in the list.
 *
 * Note: In case we need to adapt or absorb any contract change we can do it here
 * and the rest of the app will not be affected
 * @param app: IApp
 */
export class App {
    project_id;
    title;
    description;
    last_updated_at;
    last_published_at;
    notebook_file_name;
    notebook_file_size;
    permission;
    environment;
    versioning;
    versions;
    domains;
    status;
    constructor(app) {
        this.project_id = app.project_id;
        if (app.last_published_at) {
            this.last_published_at = this.getFormatLastUpdated(app.last_published_at);
        }
        const lastVersionIndex = app.versioning.all_versions
            ? app.versioning.all_versions.length - 1
            : -1;
        if (lastVersionIndex >= 0) {
            const lastUpdated = app.versioning.all_versions[lastVersionIndex].updated_at ||
                app.versioning.all_versions[lastVersionIndex].created_at;
            if (lastUpdated) {
                this.last_updated_at = this.getFormatLastUpdated(lastUpdated);
            }
        }
        this.title = app.title;
        this.description = app.description;
        this.notebook_file_name = app.notebook_file_name;
        this.notebook_file_size = app.notebook_file_size;
        this.permission = app.permission;
        this.environment = app.environment;
        this.versioning = app.versioning;
        this.versions = app.versioning.all_versions?.map((version) => ({
            ...version,
            isLatest: app.versioning.current_version === version.version,
        }));
        this.domains = app.domains.map((domain) => ({
            ...domain,
            // @TODO this two will be removed in the future and we will use domain only
            domain: domain.domain || domain.domain_name || domain.hostname || "",
        }));
        this.status = app.status;
    }
    get domainUrl() {
        return this.domains.length > 0 && this.domains[0].domain
            ? `https://${this.domains[0].domain.split("://").slice(-1)}`
            : "";
    }
    getFormatLastUpdated(dateString) {
        if (!dateString)
            return "";
        const date = new Date(dateString);
        const day = date
            .toLocaleDateString("en-US", { weekday: "short" })
            .replace(",", "");
        const monthAndDay = date.toLocaleDateString("en-US", {
            month: "short",
            day: "numeric",
        });
        const year = date.toLocaleDateString("en-US", { year: "numeric" });
        return `${day} ${monthAndDay}, ${year}`;
    }
    get domainName() {
        return this.domains.length > 0 && this.domains[0].domain
            ? this.domains[0].domain
            : "";
    }
}
