import React from "react";
import { STATUS_APP } from "../../types";
import { AppTile } from "../Tile/AppTile";
// Function that returns the priority of the status
const STATUS_PRIORITY = {
    [STATUS_APP.PUBLISHED]: 1,
    [STATUS_APP.PUBLISHING]: 2,
    [STATUS_APP.FAILED]: 3,
    [STATUS_APP.INACTIVE]: 4,
};
// Comparison function for sorting
const compareApps = (a, b) => {
    const statusA = STATUS_PRIORITY[a.status];
    const statusB = STATUS_PRIORITY[b.status];
    // If statuses are the same, compare dates
    if (statusA === statusB) {
        const dateA = a.last_updated_at ? new Date(a.last_updated_at).getTime() : 0;
        const dateB = b.last_updated_at ? new Date(b.last_updated_at).getTime() : 0;
        return dateB - dateA; // latest comes first
    }
    // Otherwise, compare statuses
    return statusA - statusB;
};
export const AppList = ({ title, apps }) => {
    const sortedApps = apps.sort(compareApps);
    return (React.createElement(React.Fragment, null,
        React.createElement("h2", null, title),
        React.createElement("p", { className: "anaconda-app-list-desc" }, "Publish an app to share it with the world!"),
        React.createElement("div", { className: "anaconda-app-list" }, sortedApps.map((app) => (React.createElement("div", { key: app.title },
            React.createElement(AppTile, { app: app })))))));
};
