import { useAppShellStateStore } from "../state/app-shell-zustand/store";
import { TerminalIcon, publishGrayIcon } from "../icons";
import { WidgetType } from "../types";
import { ReadOnlyTerminalWidget, DeploymentWidget, MyAppsWidget, } from "../widgets";
const { addWidgetToMainArea, closeWidgetById } = useAppShellStateStore.getState();
export const useWidget = () => {
    const openWidget = (widgetType = WidgetType.APP_MANAGEMENT, app, options) => {
        // Instantiate the widget
        let instantiatedWidget;
        switch (widgetType) {
            case WidgetType.DEPLOYMENT:
                if (app) {
                    instantiatedWidget = new DeploymentWidget(app);
                    instantiatedWidget.title.label = `${widgetType}: ${app.title}`;
                    instantiatedWidget.title.icon = publishGrayIcon;
                    instantiatedWidget.id = `${widgetType}-${app.project_id}`;
                }
                break;
            case WidgetType.TERMINAL:
                if (app) {
                    instantiatedWidget = new ReadOnlyTerminalWidget(app);
                    instantiatedWidget.title.label = `${options?.label}`;
                    instantiatedWidget.title.icon = TerminalIcon;
                    instantiatedWidget.id = `${widgetType}-${app.project_id}`;
                }
                break;
            default:
                instantiatedWidget = new MyAppsWidget();
                instantiatedWidget.title.label = "Apps Management";
                instantiatedWidget.id = "apps-management";
                break;
        }
        instantiatedWidget.title.caption = instantiatedWidget.title.label;
        instantiatedWidget.title.closable = true;
        // This is to also execute the close logic of the widget when the user click in the X instead of close button
        instantiatedWidget.onBeforeDetach = () => {
            closeWidgetById(instantiatedWidget.id);
        };
        // Add the widget to the main area
        addWidgetToMainArea(instantiatedWidget, widgetType === WidgetType.APP_MANAGEMENT ? "tab-after" : "split-right");
    };
    return { openWidget };
};
