import { CommandRegistry } from "@lumino/commands";
import { RankedMenu } from "@jupyterlab/ui-components";
/**
 * MenuBuilder class provides methods for building and configuring menus.
 */
export class MenuBuilder {
    commandRegistry;
    menuItems;
    constructor() {
        this.commandRegistry = new CommandRegistry();
        this.menuItems = [[]];
    }
    /**
     * Adds a menu item to the menu.
     * @param {MenuItem} item - The menu item to add.
     * @returns {MenuBuilder} The current MenuBuilder instance for chaining.
     *
     * NOTE: We have a disabled property because by default is enabled, so for most of cases we don't need to pass the disabled property
     */
    addItem(item, payload) {
        this.commandRegistry.addCommand(item.command, {
            label: item.label,
            icon: item.icon,
            iconClass: "jp-app-menu-option-icon",
            className: "jp-app-menu-option",
            execute: () => item.execute(payload),
            isVisible: () => !item.hide,
            isEnabled: () => !item.disabled,
        });
        const currentIndex = this.menuItems.length - 1;
        this.menuItems[currentIndex] = [...this.menuItems[currentIndex], item];
        return this;
    }
    // This was added to facilitate tests, because we need to access the commandRegistry
    // since we cannot test the click event
    getCommandRegistry() {
        return this.commandRegistry;
    }
    /**
     * Will add a line separation using groups concept
     */
    addLineSeparator() {
        // Add a new array position to the menuItems array
        this.menuItems = [...this.menuItems, []];
        return this;
    }
    /**
     * Builds the menu with the configured menu items.
     * @returns {RankedMenu} The built menu.
     */
    build() {
        const menu = new RankedMenu({
            commands: this.commandRegistry,
            includeSeparators: true,
        });
        menu.node.classList.add("jp-app-menu");
        this.menuItems.forEach((group) => {
            menu.addGroup(group);
        });
        return menu;
    }
    /**
     * Opens the menu at the specified event position.
     * Should be called after creating the menu (the last method called)
     * @param {MouseEvent} evt - The event containing the position where the menu should open.
     */
    open(event, setIsVisible) {
        const menu = this.build();
        menu.aboutToClose.connect(() => {
            setIsVisible(false);
        });
        menu.open(event.clientX, event.clientY);
    }
}
