import React from "react";
import { ConfirmationDialog } from "./ConfirmationDialog";
import { Dialog, showDialog } from "@jupyterlab/apputils";
/**
 * This function will open a new dialog
 *
 * @param str
 * @returns string
 */
export async function openConfirmationDialog(obj) {
    const result = await showDialog({
        title: obj.title
            ? obj.title
            : React.createElement("img", {
                src: "data:image/svg+xml;base64," + obj?.icon,
                style: { display: "block", margin: "0 auto" },
            }),
        body: React.createElement(ConfirmationDialog, {
            description: obj?.description,
        }),
        buttons: [
            Dialog.cancelButton({
                label: obj?.cancelBtnText,
                className: obj.cancelBtnClass,
            }),
            Dialog.okButton({
                label: obj?.okBtnText,
                className: obj.okBtnClass,
                displayType: obj.okBtnType ? obj.okBtnType : "default",
            }),
        ],
    }).then((result) => {
        return result.button;
    });
    return result;
}
