import { useCallback, useState } from "react";
import { STATUS_APP } from "../../types";
import { buildFailedMenu, buildInactiveMenu, buildPublishedMenu, buildPublishingMenu, } from "./MenuApps/MenuAppHandler";
import { MenuBuilder } from "./MenuBuilder";
import { buildHelpMenu } from "./MenuHelp/MenuHelpHandler";
import { buildPublishedVersionMenu, buildUnPublishedVersionMenu, } from "./MenuVersions/MenuVersionHandler";
/**
 * useMenu hook creates and configures a menu based on the provided status.
 * @param {StatusType} status - The status used to determine the menu items.
 * @returns {{ openMenu: (evt: MouseEvent) => void, isVisible: boolean }} The object containing the openMenu function.
 */
export const useAppMenu = (app) => {
    let menuBuilder = new MenuBuilder();
    const [isVisible, setIsVisible] = useState(false);
    // Configure the menu based on the status
    switch (app.status) {
        case STATUS_APP.PUBLISHED:
            menuBuilder = buildPublishedMenu(menuBuilder, app);
            break;
        case STATUS_APP.FAILED:
            menuBuilder = buildFailedMenu(menuBuilder, app);
            break;
        case STATUS_APP.PUBLISHING:
            menuBuilder = buildPublishingMenu(menuBuilder, app);
            break;
        case STATUS_APP.INACTIVE:
            menuBuilder = buildInactiveMenu(menuBuilder, app);
            break;
    }
    const openMenu = useCallback((evt) => {
        setIsVisible(true);
        return menuBuilder.open(evt, setIsVisible);
    }, [menuBuilder]);
    return {
        isVisible,
        openMenu,
        // Since the click of the JL Luminous / JL doesn't' work inside the tests we need to export the menuBuilder
        // and call directly in the tests
        menuBuilder,
    };
};
/**
 *  useHelpMenu hook creates and configures a help menu.
 * @returns {{ openMenu: (evt: MouseEvent) => void, isVisible: boolean }} The object containing the openMenu function.
 */
export const useHelpMenu = () => {
    let menuBuilder = new MenuBuilder();
    const [isVisible, setIsVisible] = useState(false);
    menuBuilder = buildHelpMenu(menuBuilder);
    const openMenu = useCallback((evt) => {
        setIsVisible(true);
        return menuBuilder.open(evt, setIsVisible);
    }, [menuBuilder]);
    return {
        isVisible,
        openMenu,
        menuBuilder,
    };
};
/**
 *  useVersionActionMenu hook creates and configures a version action menu.
 * @returns {{ openMenu: (evt: MouseEvent) => void, isVisible: boolean }} The object containing the openMenu function.
 */
export const useVersionMenu = (menuVersionPayload) => {
    let menuBuilder = new MenuBuilder();
    const [isVisible, setIsVisible] = useState(false);
    menuBuilder = menuVersionPayload.data.isLatest
        ? buildPublishedVersionMenu(menuBuilder, menuVersionPayload)
        : buildUnPublishedVersionMenu(menuBuilder, menuVersionPayload);
    const openMenu = useCallback((evt) => {
        setIsVisible(true);
        return menuBuilder.open(evt, setIsVisible);
    }, [menuBuilder]);
    return {
        isVisible,
        openMenu,
        menuBuilder,
    };
};
