import { menuVersionActions } from "./MenuVersionActions";
/**
 *
 * handlers file is used for the following reasons:
 *
 * - If we need to sort the order of the actions
 * - If we need to add a new type of menu by status we just need to create a new buildFunction
 * - If we need to override the global config.ts to have a specific opiton by status
 */
export const buildPublishedVersionMenu = (menuBuilder, menuVersionPayload) => {
    return menuBuilder
        .addItem({ ...menuVersionActions.duplicateNotebook }, menuVersionPayload)
        .addItem({ ...menuVersionActions.copyUrl }, menuVersionPayload)
        .addItem({ ...menuVersionActions.downloadApp }, menuVersionPayload)
        .addItem({ ...menuVersionActions.deleteVersion }, menuVersionPayload);
};
export const buildUnPublishedVersionMenu = (menuBuilder, menuVersionPayload) => {
    return menuBuilder
        .addItem({ ...menuVersionActions.duplicateNotebook }, menuVersionPayload)
        .addItem({ ...menuVersionActions.downloadApp }, menuVersionPayload)
        .addItem({ ...menuVersionActions.deleteVersion }, menuVersionPayload);
};
