import React, { useEffect } from "react";
import { useAppStore } from "../state/apps-zustand/store";
import { FeaturedAppList } from "../components/List/FeaturedAppList";
import { useFeatureFlags } from "../state/apps-zustand/feature-flags";
import { FeatureFlag } from "../types";
export const FeaturedAppsView = () => {
    const { featuredApps, getFeaturedApps } = useAppStore();
    const store = useFeatureFlags.getState();
    const isFeaturedAppsFFEnabled = store.evaluateFeatureFlag(FeatureFlag.FEATURED_APPS);
    useEffect(() => {
        getFeaturedApps();
    }, []);
    return (isFeaturedAppsFFEnabled &&
        featuredApps.length > 0 && React.createElement(FeaturedAppList, { featuredApps: featuredApps }));
};
