import { parseParams } from "@anaconda/handle_cookie_lib";
import { warnUserOfNotebookLoad } from "./components/Dialog/warn_user_of_notebook_load";
import { loadNotebookFromURL } from "./components/utils/load_notebook";
export default class NotebookLoadPlugin {
    async activate(app, opts) {
        const coreReady = await opts.corePlugin.ready();
        if (!coreReady.featureFlag.getBool("monorepo-share-notebooks", false)) {
            return;
        }
        console.log("[Monorepo] Enabling the notebook-load plugin");
        const notebook_load_params = parseParams("notebook_params");
        if (notebook_load_params?.nb_url) {
            if (await warnUserOfNotebookLoad(notebook_load_params.nb_url)) {
                loadNotebookFromURL(app, notebook_load_params.nb_url);
            }
        }
    }
}
