import { fetchNotebookData } from "../api";
import { INotification } from "jupyterlab_toastify";
export async function setNewNotebookName(notebook, path, notebook_title, index = 0) {
    const newFileName = `${notebook_title}${index ? "_" + index : ""}.ipynb`;
    try {
        await notebook.context.rename(newFileName);
        await notebook.context.save();
    }
    catch (error) {
        return setNewNotebookName(notebook, path, notebook_title, ++index);
    }
}
export async function ensureDirectoriesExist(path, app) {
    const parts = path.split("/");
    let currentPath = "/";
    for (const part of parts) {
        // Check if the current part of the path exists
        try {
            await app.serviceManager.contents.get(`${currentPath}${part}`);
        }
        catch (error) {
            // If not, create a new directory and rename it
            const untitledDir = await app.serviceManager.contents.newUntitled({
                path: currentPath,
                type: "directory",
            });
            await app.serviceManager.contents.rename(untitledDir.path, `${currentPath}${part}`);
        }
        // Update currentPath for the next iteration
        currentPath = `${currentPath}${part}/`;
    }
}
export async function openNotebook({ app, notebook_title, notebook_path, path = "", }) {
    try {
        await app.restored;
        if (path) {
            await ensureDirectoriesExist(path, app);
        }
        const notebook = await app.commands.execute("notebook:create-new", { cwd: `/${path}` });
        setNewNotebookName(notebook, path, notebook_title);
        const notebookData = await fetchNotebookData(notebook_path, 1);
        notebook.context.model.fromJSON({
            ...notebookData,
        });
    }
    catch (error) {
        console.error(error);
    }
}
export async function copyToClipboard(text, successMessage = "Copied to clipboard", errorMessage = "Failed to copy to clipboard") {
    try {
        await navigator.clipboard.writeText(text);
        INotification.success(successMessage, {
            autoClose: 5000,
        });
    }
    catch (error) {
        INotification.error(errorMessage, {
            autoClose: 5000,
        });
        console.error(errorMessage, error);
    }
}
// Utility functions
export const findCategoryByTitle = (categories, title) => categories.find((category) => category.title === title);
export const findCourseByTitle = (category, title) => category?.courses.find((course) => course.title === title);
export const findNotebookByTitle = (course, title) => course?.notebooks.find((notebook) => notebook.title === title);
