import { closeIcon, downloadIcon } from "@jupyterlab/ui-components/lib/icon";
import { Notification } from "@jupyterlab/apputils";
import { ViewIcon, ViewJupyterLabIcon, UnPublishIcon, RestartIcon, CopyLinkIcon, ViewLogsIcon, publishGrayIcon, SVGIcons, infoIcon, } from "../../../icons";
import { useAppStore } from "../../../state/apps-zustand/store";
import { App, STATUS_APP, WidgetType } from "../../../types";
import { openConfirmationDialog } from "../../ConfirmationDialog";
import { downloadApp } from "../../../api/project";
import { PANEL_CMD } from "../../../views/createAppView";
import { useAppShellStateStore } from "../../../state/app-shell-zustand/store";
import { useWidget } from "../../../hooks/OpenWidgetHook";
import { PROD_NUCLEUS_URL } from "../../../constants";
import { showViewAppModal } from "../../AppModal/AppModal";
import { openInNewTab, downloadFile, base64ToBlob } from "../../../utils/index";
import { showVersionsModal } from "../../VersionsModal/VersionsModal";
import { useHeapAnalytics } from "../../../state/apps-zustand/heap";
/**
 * This is a global config object used for all the menus we have, since a specific option is the same when appear in some menu,
 * we have created this method to make easier to build the menus.
 *
 * If we need to override some specific option by a specific status we can do this on the
 * handlers file
 */
export const menuAppActions = {
    viewInNewTab: {
        label: "View in New Browser Tab",
        icon: ViewIcon,
        command: "view:view-in-new-tab",
        execute: (app) => {
            const { registerEvent } = useHeapAnalytics.getState();
            registerEvent("nb.anaconda.cloud - Panel - Click - View in New Browser Tab");
            openInNewTab(app?.domainUrl);
        },
    },
    viewInJupyterLab: {
        label: "View in JupyterLab",
        icon: ViewJupyterLabIcon,
        command: "view:view-in-jupyter-lab",
        execute: (app) => {
            const { registerEvent } = useHeapAnalytics.getState();
            registerEvent("nb.anaconda.cloud - Panel - Click - View in JupyterLab");
            const { openWidget } = useWidget();
            // The logic to open in a viewInJupyterLab is inside the widgets/StatusPageWidget.tsx
            if (app) {
                openWidget(WidgetType.DEPLOYMENT, app);
            }
        },
    },
    publish: {
        label: "Publish",
        icon: publishGrayIcon,
        command: "publish:publish",
        execute: async (app) => {
            if (app === undefined) {
                return;
            }
            const { registerEvent } = useHeapAnalytics.getState();
            registerEvent("nb.anaconda.cloud - Panel - Click - Publish app");
            const { closeAllWidgetsByApp } = useAppShellStateStore.getState();
            const { openWidget } = useWidget();
            const { publishApp, createApp } = useAppStore.getState();
            Notification.success(`Publishing ${app.title}`, {
                autoClose: 5000,
            });
            // Invoke createApp if domain not found, otherwise publishApp
            const domainApp = app.domainName
                ? await publishApp(app.domainName)
                : await createApp(app.project_id, PANEL_CMD);
            if (domainApp === null) {
                closeAllWidgetsByApp(app);
                openWidget(WidgetType.APP_MANAGEMENT, app);
                return;
            }
            closeAllWidgetsByApp(app);
            openWidget(WidgetType.APP_MANAGEMENT, app);
        },
    },
    unpublish: {
        label: "Unpublish",
        icon: UnPublishIcon,
        command: "unpublish:unpublish",
        execute: async (app) => {
            const { registerEvent } = useHeapAnalytics.getState();
            registerEvent("nb.anaconda.cloud - Panel - Click - Unpublish app");
            const dialog = {
                icon: SVGIcons.WARN_SVG,
                description: `Are you sure you want to unpublish the app "${app?.title}"? Your app will become inactive.`,
                okBtnText: "Unpublish",
                okBtnClass: "jp-delete-btn",
                cancelBtnText: "Cancel",
                cancelBtnClass: "jp-cancel-btn",
                okBtnType: "warn",
            };
            const dialogResult = await openConfirmationDialog(dialog);
            if (dialogResult.accept && app) {
                const { unpublishApp } = useAppStore.getState();
                Notification.success(`Unpublishing ${app.title}`, {
                    autoClose: 5000,
                });
                const hasSuccess = await unpublishApp(app.domainName);
                if (hasSuccess) {
                    Notification.success(`${app.title} unpublished`, {
                        autoClose: 5000,
                    });
                }
                else {
                    Notification.error("An error occurred while unpublishing the app", {
                        autoClose: 5000,
                    });
                }
            }
        },
    },
    restart: {
        label: "Restart",
        icon: RestartIcon,
        command: "restart:restart",
        execute: async (app) => {
            if (app === undefined) {
                return;
            }
            const { registerEvent } = useHeapAnalytics.getState();
            registerEvent("nb.anaconda.cloud - Panel - Click - Restart app");
            const { restartApp } = useAppStore.getState();
            const { closeAllWidgetsByApp } = useAppShellStateStore.getState();
            const { openWidget } = useWidget();
            Notification.success(`Restarting ${app.title}`, {
                autoClose: 5000,
            });
            const domainApp = await restartApp(app?.domainName);
            if (domainApp === null) {
                closeAllWidgetsByApp(app);
                openWidget(WidgetType.APP_MANAGEMENT, app);
                return;
            }
            const appDomains = [domainApp];
            const status = STATUS_APP.PUBLISHING;
            const appWithDomain = new App({ ...app, status, domains: appDomains });
            closeAllWidgetsByApp(app);
            openWidget(WidgetType.APP_MANAGEMENT, appWithDomain);
        },
    },
    copyAppLink: {
        label: "Copy App Link",
        icon: CopyLinkIcon,
        command: "copy:copy-app-link",
        execute: async (app) => {
            const { registerEvent } = useHeapAnalytics.getState();
            registerEvent("nb.anaconda.cloud - Panel - Click - Copy App Link");
            if (app && app.domainUrl) {
                try {
                    await navigator.clipboard.writeText(app.domainUrl);
                    Notification.success("Link copied to clipboard", {
                        autoClose: 5000,
                    });
                }
                catch (err) {
                    Notification.error("An error occurred while copying the link", {
                        autoClose: 5000,
                    });
                }
            }
            else {
                console.warn("App or app domainUrl is not defined");
            }
        },
    },
    viewServerLogs: {
        label: "View Server Logs",
        icon: ViewLogsIcon,
        command: "view-server-logs:view-server-logs",
        execute: (app) => {
            const { registerEvent } = useHeapAnalytics.getState();
            registerEvent("nb.anaconda.cloud - Panel - Click - View Server Logs");
            if (app === undefined) {
                return;
            }
            const { openWidget } = useWidget();
            openWidget(WidgetType.TERMINAL, app, {
                label: `Server Logs: ${app.title}`,
            });
        },
    },
    viewErrorLogs: {
        label: "View Error Logs",
        icon: ViewLogsIcon,
        command: "view-error-logs:view-error-logs",
        execute: (app) => {
            const { registerEvent } = useHeapAnalytics.getState();
            registerEvent("nb.anaconda.cloud - Panel - Click - View Error Logs");
            if (app === undefined) {
                return;
            }
            const { openWidget } = useWidget();
            openWidget(WidgetType.TERMINAL, app, {
                label: `Error Logs: ${app.title}`,
            });
        },
    },
    viewConfiguration: {
        label: "View Configuration",
        icon: infoIcon,
        command: "view-config:view-config",
        execute: (app) => {
            const { registerEvent } = useHeapAnalytics.getState();
            registerEvent("nb.anaconda.cloud - Panel - Click - View Configuration");
            if (app) {
                showViewAppModal(app);
            }
        },
    },
    download: {
        label: "Download",
        icon: downloadIcon,
        command: "download:download",
        execute: async (app) => {
            const { registerEvent } = useHeapAnalytics.getState();
            registerEvent("nb.anaconda.cloud - Panel - Click - Download");
            Notification.success("Your download will begin shortly.", {
                autoClose: 5000,
            });
            // Define the file name
            const fileName = `${app.title}-${app.versioning.current_version}.zip`;
            // Define the mime type
            const mimeType = "application/zip";
            // Get the base64 string of the app
            const appDownloaded = await downloadApp(app.project_id, app.versioning.current_version);
            // Now we need to convert the appDownloaded string from base64 to a file and download it
            const blob = base64ToBlob(appDownloaded.archive, mimeType);
            // download the Blob file
            downloadFile(blob, fileName);
        },
    },
    delete: {
        label: "Delete",
        icon: closeIcon,
        command: "delete:delete",
        execute: (app) => {
            const { registerEvent } = useHeapAnalytics.getState();
            registerEvent("nb.anaconda.cloud - Panel - Click - Delete");
            const dialog = {
                icon: SVGIcons.WARN_SVG,
                description: `Are you sure you want to delete the app "${app?.title}"? The associated URL will no longer be available.`,
                okBtnText: "Delete",
                okBtnClass: "jp-delete-btn",
                cancelBtnText: "Cancel",
                cancelBtnClass: "jp-cancel-btn",
                okBtnType: "warn",
            };
            openConfirmationDialog(dialog).then(async (dialogResult) => {
                if (dialogResult.accept && app) {
                    const { deleteApp } = useAppStore.getState();
                    const { closeAllWidgetsByApp } = useAppShellStateStore.getState();
                    Notification.success(`Deleting ${app.title}`, {
                        autoClose: 5000,
                    });
                    const hasSuccess = await deleteApp(app.project_id, app.domainName);
                    closeAllWidgetsByApp(app);
                    if (hasSuccess) {
                        Notification.success(`${app.title} deleted`, {
                            autoClose: 5000,
                        });
                    }
                    else {
                        Notification.error("An error occurred while deleting the app", {
                            autoClose: 5000,
                        });
                    }
                }
            });
        },
    },
    upgradeToPublish: {
        label: "Upgrade to Publish",
        command: "upgrade-to-publish:upgrade-to-publish",
        icon: publishGrayIcon,
        // We don't need the app parameter there, if we need in the future check the
        // PublishAppFormView.tsx where this command is used
        execute: () => {
            const { registerEvent } = useHeapAnalytics.getState();
            registerEvent("nb.anaconda.cloud - Panel - Click - Upgrade to Publish");
            window.open(`${PROD_NUCLEUS_URL}/pricing`, "_blank");
        },
    },
    lastOptionMessage: {
        label: "Shift+Right Click for Browser Menu",
        command: "last-option-message:last-option-message",
        disabled: true,
        // Will not have implementation because will be always disabled
        // eslint-disable-next-line @typescript-eslint/no-empty-function
        execute: (app) => { },
    },
    viewVersions: {
        label: "View Versions",
        command: "view-versions:view-versions",
        execute: (app) => {
            const { registerEvent } = useHeapAnalytics.getState();
            registerEvent("nb.anaconda.cloud - Panel - Click - View Versions");
            if (app) {
                showVersionsModal(app);
            }
        },
    },
};
