import React from "react";
import ReactDOM from "react-dom";
import { Widget } from "@lumino/widgets";
import { ReadOnlyTerminal } from "../components/Terminal/ReadOnlyTerminal";
export class ReadOnlyTerminalWidget extends Widget {
    _app;
    constructor(app) {
        super();
        this._app = app;
        this.addClass("jp-CustomWidget");
        this.addClass("jp-ReadOnlyTerminal");
    }
    onAfterAttach() {
        ReactDOM.render(React.createElement(ReadOnlyTerminal, { app: this._app }), this.node);
    }
    onBeforeDetach() {
        ReactDOM.unmountComponentAtNode(this.node);
    }
}
