import { DOMUtils, MainAreaWidget, WidgetTracker } from "@jupyterlab/apputils";
import { parseParams } from "@anaconda/handle_cookie_lib";
import { LMSWidget } from "./widgets";
import { useHeapAnalytics } from "./store/heap";
import { useCloudApiUrl } from "./store/apiUrl";
const lmsNotebooksLauncherCommand = "anaconda-learning-launcher:open";
const lmsNotebooksLauncherCategory = "Anaconda";
const lmsNotebooksLauncherWidgetTitleLabel = "Anaconda Learning";
const lmsNotebooksLauncherLabel = "Anaconda Learning";
export function actOnCookieContents() {
    const lms_params = parseParams("lms_params");
    if (lms_params && lms_params?.open_lms) {
        return {
            openLMS: true,
            categoryTitle: lms_params?.category_title,
            courseTitle: lms_params?.course_title,
            notebookTitle: lms_params?.notebook_title,
        };
    }
    return null;
}
export default class Plugin {
    async activate(app, opts) {
        const coreReady = await opts.corePlugin.ready();
        if (!coreReady.featureFlag.getBool("monorepo-lms-notebooks", false)) {
            return;
        }
        console.log("[Monorepo] Enabling the lms-notebooks plugin");
        const LMSConfigParams = actOnCookieContents();
        if (LMSConfigParams) {
            app.restored.then(() => {
                app.commands.execute(lmsNotebooksLauncherCommand);
            });
        }
        // initialize heap analytics store
        const { setHeapAnalytics } = useHeapAnalytics.getState();
        setHeapAnalytics(coreReady.analytics.heap);
        // initialize cloud api url store
        const { setApiUrl } = useCloudApiUrl.getState();
        setApiUrl(coreReady.config.cloud.url);
        let lmsWidget;
        app.commands.addCommand(lmsNotebooksLauncherCommand, {
            label: lmsNotebooksLauncherLabel,
            iconClass: "anaconda-data-catalog-icon",
            execute: async () => {
                const tracker = new WidgetTracker({
                    namespace: "anaconda-learning-launcher",
                });
                try {
                    if (!lmsWidget || lmsWidget.isDisposed) {
                        const content = new LMSWidget(app, coreReady.config, LMSConfigParams);
                        lmsWidget = new MainAreaWidget({
                            content,
                        });
                        lmsWidget.id = DOMUtils.createDomID();
                        lmsWidget.title.label = lmsNotebooksLauncherWidgetTitleLabel;
                        lmsWidget.title.closable = true;
                    }
                    if (!tracker.has(lmsWidget)) {
                        // Track the state of the widget for later restoration
                        tracker.add(lmsWidget);
                    }
                    if (!lmsWidget.isAttached) {
                        // Attach the widget to the main work area if it's not there
                        app.shell.add(lmsWidget, "main");
                    }
                    // Activate the widget
                    app.shell.activateById(lmsWidget.id);
                }
                catch (err) {
                    //@TODO: Implement Datadog alerts
                    console.error("LMS - error", err);
                }
            },
        });
        opts.launcher.add({
            command: lmsNotebooksLauncherCommand,
            category: lmsNotebooksLauncherCategory,
        });
    }
}
